<template>
	<view class="content">
		<u-swiper :list="slides" name="img_url"></u-swiper>
		<!-- 导航栏 -->
		<view class="shop-nav u-m-t-20">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="changeNav"></u-tabs>
		</view>
		
		<!-- 商品 -->
		<view class="wrap">
			<u-row gutter="16">
				<u-col span="6" v-for="goods in goodsList" class="u-m-t-20" >
					<!-- <view class="demo-layout bg-purple"> -->
						<navigator class="goods-item">
							<u-image width="100%" height="300rpx" :src="goods.cover_url" :fade="true" duration="450">
								<u-loading slot="loading"></u-loading>
							</u-image>
							<view class="title u-text-center u-m-t-10 u-m-b-20 u-line-1">
								{{goods.title}}
							</view>
							<view class="describe">
								<view class="price">
									￥ {{goods.price}}
								</view>
								<view class="sales">
									销量：{{goods.sales}}
								</view>
							</view>
						</navigator>
					<!-- </view> -->
				</u-col>
			</u-row>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 轮播图
				slides: [],
				// 商品
				goodsList: [],

				list: [
					{name: '默认'}, 
					{name: '销量'}, 
					{name: '推荐'},
					{name: '最新'}
				],
				current: 0,
				
				page: 1,
			}
		},
		onLoad() {
			// 异步获取首页数据
			// let res = await this.$u.get('/api/index?page=1')
			// let res = await this.$u.post('/api/auth/wx/bind',{type:'ubind',openid:123456})
			// let res = await this.$u.patch('/api/orders/1/confirm',)
			
			// 获取vuex数据
			// console.log(this.vuex_version);
			this.getIndexData()
		},
		methods: {
			changeNav(index) {
				// 当前导航条下标
				this.goodsList = [];
				this.page = 1
				this.current = index;
				this.getIndexData()
			},
			// 获取首页数据函数
			async getIndexData () {
				// 获取首页数据
				const params = {
					page: this.page
				}
				if (this.current === 1) params.sales = 1
				if (this.current === 2) params.recommend = 1
				if (this.current === 3) params.new = 1
				let res = await this.$u.api.getIndex(params)
				// 获取轮播图
				this.slides = res.data.slides;
				// 获取首页商品数据
				this.goodsList = [...this.goodsList, ...res.data.goods.data];
			}
		},
		
		// 滚动刷新下一页或者更多商品数据
		onReachBottom () {
			this.page++
			this.getIndexData()
		}
	}
</script>

<style lang="scss" scoped>
	.goods-item {
		padding: 10px;
		.title {
			font-weight: 800;
			font-size: 32rpx;
		}
		.describe {
			display: flex;
			justify-content: space-between;
			.price {
				color: red;
			}
			.sales {
				color: #999
			}
		}
	}
</style>
